<template>
  <div  style="width:50%;margin-left:25%" >
<el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm">
  <el-form-item label="用户名" prop="username">
    <el-input type = "text" v-model="ruleForm.username" placeholder="用户名" auto-complete="off" ref="username"></el-input>
  </el-form-item>
  <el-form-item label="密码" prop="password">
   <el-input type="password" v-model="ruleForm.password" placeholder="密码" auto-complete="off" ref="password"></el-input>
  </el-form-item>
  <el-button @click="redirect">注册</el-button>
  <el-button type="primary" @click="submit('ruleForm')" >登录</el-button>
</el-form>
  </div>
</template>
<script>
import { Message } from 'element-ui';
export default {
  data() {
    var checkUser = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("用户名不能为空"));
      } else {
        callback();
      }
    };
    var checkPass = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("密码不能为空"));
      } else {
        callback();
      }
    };

    return {
      ruleForm: {
        username: "",
        password: ""
      },
      rules: {
        username: [{ validator: checkUser, trigger: "blur" }],
        password: [{ validator: checkPass, trigger: "blur" }]
      }
    };
  },

  methods: {
    submit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$http({
              url: "http://120.24.5.178:8100/user/login",
              method: "POST",
              body: JSON.stringify({
                username: this.ruleForm.username,
                password: this.ruleForm.password
              }),
              headers: {
                "Content-Type": "application/json"
              }
            })
            .then(
              function(response) {
                if (response.data.code === 0) {
                  Message.success("success");
                  this.$router.push({ name: "list" });
                }else {
                   Message.warning(response.data.msg)
                }
              }
            );
        }
      });
    },
    redirect() {
      this.$router.push({ name: "register" });
    }
  }
};
</script>




